@font-face{
    font-family: 'Quicksand';
    font-weight: bold;
    src: url('./Quicksand-Bold.ttf')
}
@font-face{
    font-family: 'Quicksand';
    font-weight: normal;
    src: url('./Quicksand-Regular.ttf')
}

@font-face{
    font-family: 'Poppins';
    font-weight: bold;
    src: url('./Poppins-Bold.ttf')
}
@font-face{
    font-family: 'Poppins';
    font-weight: normal;
    src: url('./Poppins-Regular.ttf')
}


body {
    line-height: 1;
    margin: 0px;
}

#main p {
    margin-top: 2vw;
    font-family: 'Poppins';
    font-weight: bold;
    color: #5D5D5D;
}

#header {
    width: 70%;
}

#logo {
    width: auto;
    max-width: 10vw;
    height: auto;
    display: block;
    z-index: 2;
    padding-top: 8vw;
    padding-bottom: 20vw;
    margin-left: auto;
    margin-right: auto;
}

#cont1 {
    margin: 0px;
    padding-top: 3%;
    width: 100%;
    background-color: #F0F0F0;
    padding-left: 5%;
    float: left;
    line-height: 1;
    position: absolute;
}

.line {
    background-color: #36A9E1;
    height: 0.2vw;
    width: 30%;
    display: inline-block;
    margin-top: 0%;
    margin-bottom: 5%;
}

.title {
    font-weight: bold !important;
    font-size: 5vw !important;
    padding-bottom: 1%;
}

.subtitle {
    font-weight: 500;
    font-size: 3vw;
}

.summary {
    line-height: 1.6;
    font-size: 1.8vw;
    color:#333333;
    font-family: 'Quicksand';
    font-weight: bold;
}

.summary2 {
    line-height: 1.6;
    font-size: 1.5vw;
    color:#5D5D5D;
    font-family: 'Quicksand';
    font-weight: normal;
    padding-top: 3vw;
    padding-right: 10vw;
}

.img {
    float: left;
    margin-left: 10%;
    margin-top: 2%;
    height: 30vw;
}

#rpi {
    float: left;
    height: 20vw;
}

#dron {
    height: 15vw;
}

.vl {
    border-left: 4px solid #36A9E1;
    height: 15vw;
  }

#mobile {
    height: 15vw;
    padding-top: 5vw;
}

.summary-blue {
    font-family: 'Quicksand';
    font-size: 2vw;
    color: #000000;
    padding-left: 2vw;
    margin-right: 3vw;
    float: left;
}

.title-blue {
    font-family: 'Poppins';
    font-weight: bold;
    font-size: 5vw;
    color: #000000;
    margin-right: 3vw;
    margin-top: 5vw;
    width: 50%;
    float: right;
}

#youtube {
    height: 25vw;
    float: left;
    padding-left: 10vw;
}

#image-blue {
    display: contents;
}

.language{
    text-align: end;
}
img.language  {
    float: right;
    margin-top: -1vw;
    margin-right: -0.5vw;
}