@font-face{
    font-family: 'Quicksand';
    font-weight: bold;
    src: url('./Quicksand-Bold.ttf')
}
@font-face{
    font-family: 'Quicksand';
    font-weight: normal;
    src: url('./Quicksand-Regular.ttf')
}

@font-face{
    font-family: 'Poppins';
    font-weight: bold;
    src: url('./Poppins-Bold.ttf')
}
@font-face{
    font-family: 'Poppins';
    font-weight: normal;
    src: url('./Poppins-Regular.ttf')
}


body {
    line-height: 1;
    margin: 0px;
    background-image: url('./fondo.png');
    background-position-y: 0px;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 1;                             /*afecta a la opacidad del contenedor*/
}

#main_container {
    border:none;
    background-color: #F0F0F0;
}

.cover {
    border: none;
    width: 100%;
    position: relative;
}

.cover img {
    width: 100%;
    position: absolute;
}

#cover {
    z-index: 1;
}

#logo {
    width: auto;
    max-width: 10vw;
    height: auto;
    display: block;
    z-index: 2;
    padding-top: 8vw;
    padding-bottom: 20vw;
    margin-left: auto;
    margin-right: auto;
}

#cont1 {
    margin: 0px;
    padding-top: 3%;
    width: 100%;
    background-color: #F0F0F0;
    padding-left: 5%;
    float: left;
    line-height: 1;
    position: absolute;
}

.line {
    background-color: #36A9E1;
    height: 0.2vw;
    width: 30%;
    display: inline-block;
    margin-top: 0%;
    margin-bottom: 5%;
}

.title {
    font-size: 1.2vw;
    padding-bottom: 0%;
    margin-top: 10%;
    margin-bottom: 1%;
    font-family: 'Poppins';
    font-weight: normal;
    src: url('./Poppins-Regular.ttf')
}

.title1 {
    font-size: 1.2vw;
    padding-bottom: 0%;
    margin-top: 10%;
    margin-right: 400px;
    margin-bottom: 1%;
    font-family: 'Poppins';
    font-weight: normal;
    src: url('./Poppins-Regular.ttf')
}

.title2 {
    font-size: 1.2vw;
    padding-bottom: 0%;
    margin-top: 10%;
    margin-left: 100px;
    margin-bottom: 1%;
    font-family: 'Poppins';
    font-weight: normal;
    src: url('./Poppins-Regular.ttf')
}

.title3 {
    font-weight: 500;
    font-size: 3vw;
    font-family: 'Poppins';
    font-weight: bold;
    src: url('./Poppins-Bold.ttf')
}

.summary {
    line-height: 1.6;
    font-size: 2vw;
    color:#333333;
    font-family: 'Quicksand'
}

.Flecha {
    float: left;
    margin-left: 5%;
    margin-top: 4%;
    height: 2.2vw;
}

.Aro {
    float: left;
    margin-left: 5%;
    margin-top: 4%;
    height: 4.2vw;
}

.button1 {
    background-color: #36A9E1; /* Blue */
    border: none;
    color: black;
    padding: 8px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    margin: 250px 250px;
    cursor: pointer;
    border-radius: 40px;
    font: "Poppins";
    font-weight: bold;
    margin-left: 80%;
    margin-top: 2%;
  }